body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f0f2f5;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.ant-layout {
  min-height: 100vh;
}

.site-layout-content {
  padding: 24px;
  background: #fff;
  min-height: 280px;
}

.logo {
  height: 32px;
  margin: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 18px;
  font-weight: bold;
}

.card-container {
  margin-bottom: 24px;
}

.dashboard-card {
  height: 100%;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.07);
  transition: all 0.3s;
}

.dashboard-card:hover {
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.1);
  transform: translateY(-2px);
}

.chart-container {
  height: 300px;
  width: 100%;
}

.stat-card {
  text-align: center;
  padding: 20px;
}

.stat-card .ant-statistic-title {
  font-size: 16px;
  margin-bottom: 12px;
}

.stat-card .ant-statistic-content {
  font-size: 24px;
  font-weight: bold;
}

.trend-up {
  color: #52c41a;
}

.trend-down {
  color: #f5222d;
}